// This needs to match /public/version.json
const date = process.env.REACT_APP_BUILD_DATE as string;
const appVersion = process.env.REACT_APP_VERSION as string;
const commit = process.env.REACT_APP_COMMIT as string | 'development';
const deployment = (process.env.REACT_APP_DEPLOYMENT ?? 'development') as 'production' | 'testing' | 'development';
const backend: 'production' | 'testing' = window.localStorage['backend'] ?? (deployment === 'production' ? 'production' : 'testing');
const bundleApplication = (process.env.REACT_APP_APPLICATION as 'steam' | 'gymapp' | 'gymweb' | 'all') ?? 'all';
export const version = new class {
  date = date;
  version = appVersion;
  commit = commit;
  deployment = deployment;
  backend = backend;
  bundleApplication = bundleApplication;
  isDevelopment = !commit || commit === 'development';
  get build() { return bundleApplication + '_' + this.deployment + '_' + this.date + '_' + this.version + '_' + this.commit }
}();
console.log('DERKVERSION', JSON.stringify(version, null, 2))