/* eslint-disable import/first */
import { version } from './version';
// tslint:disable-next-line: no-console
console.log(`Dr. Derk's Mutant Battlegrounds version`, version.build);
import * as Sentry from '@sentry/browser';

if (!version.isDevelopment) {
  Sentry.init({
    dsn: process.env.REACT_APP_SENTRY_DSN,
    release: version.build
  });
}

const appVersion = document.createElement('div');
appVersion.id = 'appVersion';
appVersion.appendChild(document.createTextNode(version.build));
document.documentElement!.appendChild(appVersion);


import moment from 'moment';
import * as momentDurationFormatSetup from 'moment-duration-format';
momentDurationFormatSetup(moment);
import './firebase';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import './index.css';
// import registerServiceWorker from './registerServiceWorker';
import "flexboxgrid/dist/flexboxgrid.css";
import '@fortawesome/fontawesome-free/css/all.css';
import 'animate.css/animate.css';

// https://blog.logrocket.com/multiple-entry-points-in-create-react-app-without-ejecting/
const bundleApplication = process.env.REACT_APP_APPLICATION === 'steam' ? import(`./SteamApp/App`)
  : process.env.REACT_APP_APPLICATION === 'gymapp' ? import('./GymApp')
  : process.env.REACT_APP_APPLICATION === 'gymweb' ? import('./GymWeb')
  : import('./AllApp');

bundleApplication.then(({ default: BuildTarget }) => {
  ReactDOM.render(
    <BuildTarget />,
    document.getElementById('root') as HTMLElement
  );
})

// registerServiceWorker();
